:root {
  --_basic-black: #1b1f2b;
  --_basic-white: #ffffff;
  --_orange: #efa501;
  --_green: #286562;
  --_gray: #a6a6a6;
  --_beige: #f4f0e9;
  --_semi-orange: #f8e6be;
  --_semi-green: #e0f0e5;
  --_semi-gray: #f1f3f2;
  --_semi-beige: #f9f7f4;
  --_dark-green: #3b737b;
  --_dark-gray: #d9dfdb;
  --_special-green: #627f7e;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Bold.woff2") format("woff2"), url("../fonts/Roboto-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
*,
*::after,
*::before {
  box-sizing: border-box;
}

/* Images */
img {
  display: block;
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", "Aria", sans-serif;
  font-size: 13px;
  line-height: 1em;
  min-height: 100%;
  min-width: 360px;
  background-color: var(--_semi-beige);
  color: var(--_basic-black);
}

.title-content p {
  width: 318px;
  margin: 0 auto;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  color: var(--_gray);
  text-align: center;
}
@media (min-width: 768px) {
  .title-content {
    margin-bottom: 100px;
  }
  .title-content p {
    width: 100%;
    padding-left: 150px;
    padding-right: 150px;
    font-size: 20px;
    line-height: 28px;
  }
}

a {
  font-family: inherit;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.title {
  margin: 0;
  font-size: 34px;
  font-weight: 700;
  line-height: 38px;
  letter-spacing: 0.68px;
  color: var(--_basic-black);
  text-align: center;
}
@media (min-width: 768px) {
  .title {
    font-size: 59px;
    font-weight: 700;
    line-height: 73px;
    letter-spacing: 0;
  }
}

.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 10px 31px;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  font-family: "Roboto", sans-serif;
  border: none;
  border-radius: 10px;
  width: 50%;
  cursor: pointer;
}
@media (min-width: 768px) {
  .button {
    font-size: 20px;
    line-height: 28px;
  }
}

.container {
  width: 100%;
  padding: 0 21px;
  margin: 0 auto;
  max-width: 1200px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 53px;
  }
}
@media (min-width: 1200px) {
  .container {
    padding: 0 15px;
  }
}

.header {
  position: absolute;
  width: 100%;
  padding: 33px 20px;
  min-height: 31px;
  color: var(--_basic-white);
  z-index: 11;
}
.header a {
  color: inherit;
  text-decoration: none;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
}
@media (min-width: 768px) {
  .header {
    padding: 29px 57px;
  }
  .header a {
    font-size: 20px;
    line-height: 28px;
  }
}
.header--scroll {
  position: fixed;
  background: rgba(243, 239, 232, 0.6);
  backdrop-filter: blur(27px);
  z-index: 99;
  color: var(--_basic-black);
}
.header--scroll .navigation__phone {
  font-size: 20px;
}
.header--scroll .navigation__phone::after,
.header--scroll .navigation__link::after {
  background-color: var(--_basic-black);
}

.header__inner {
  max-width: 1170px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
}

.header__logo {
  display: flex;
  height: 22px;
  width: 133px;
}
.header__logo a svg {
  width: 100%;
  height: 100%;
}
@media (min-width: 768px) {
  .header__logo {
    height: 30px;
    width: 181px;
  }
  .header__logo svg {
    width: 100%;
    height: 100%;
  }
}

.navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: none;
}
@media (min-width: 920px) {
  .navigation__list {
    display: flex;
    gap: 30px;
  }
}
@media (min-width: 1200px) {
  .navigation__list {
    gap: 50px;
  }
}

.navigation__link,
.navigation__phone {
  display: inline-block;
  position: relative;
}
.navigation__link::after,
.navigation__phone::after {
  content: "";
  position: absolute;
  height: 1px;
  background-color: var(--_basic-white);
  width: 0;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  opacity: 0;
  transition: width 0.5s, opacity 0.5s;
}
.navigation__link:hover::after,
.navigation__phone:hover::after {
  width: 100%;
  opacity: 1;
}

.hero {
  position: relative;
  display: flex;
  height: 100vh;
}

.hero__inner {
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0) 100%), linear-gradient(0deg, rgba(0, 0, 0, 0.3) 0%, rgba(0, 0, 0, 0.3) 100%), url("../img/bg-header.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.hero__content {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.hero__content h1 {
  margin: 0;
  margin-top: 85px;
  margin-bottom: 42px;
  font-size: 34px;
  font-weight: 700;
  line-height: 38px;
  padding-left: 20px;
  padding-right: 20px;
  letter-spacing: 0.68px;
  max-width: 769px;
  width: 360px;
  color: var(--_basic-white);
  text-align: center;
}
.hero__content p {
  margin: 0;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  color: var(--_basic-white);
  margin-bottom: 40px;
  text-align: center;
  padding-right: 30px;
  padding-left: 30px;
  max-width: 602px;
}
.hero__content a {
  display: inline-block;
  text-decoration: none;
  color: inherit;
  padding: 5px 20px;
  background-color: var(--_basic-white);
  border: 0;
  border-radius: 10px;
  transition: color 0.5s, background-color 0.5s;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  letter-spacing: -0.16px;
}
@media (any-hover: hover) {
  .hero__content a:hover {
    color: var(--_basic-white);
    background-color: var(--_orange);
  }
}
@media (min-width: 768px) {
  .hero__content h1 {
    font-size: 96px;
    line-height: 104px;
    letter-spacing: 1.92px;
    margin-bottom: 55px;
    width: 100%;
  }
  .hero__content p {
    width: 100%;
    font-size: 20px;
    line-height: 28px;
    padding-left: 80px;
    padding-right: 80px;
    margin-bottom: 50px;
  }
  .hero__content a {
    font-size: 17px;
    line-height: 24px;
    letter-spacing: -0.17px;
  }
}
@media (min-width: 1200px) {
  .hero__content h1 {
    margin-top: 90px;
  }
  .hero__content p {
    padding-right: 10px;
    padding-left: 10px;
  }
}

.tour {
  padding-top: 75px;
}
@media (min-width: 768px) {
  .tour {
    padding-top: 135px;
  }
}

.tour__title {
  margin-bottom: 31px;
}
@media (min-width: 768px) {
  .tour__title {
    margin-bottom: 36px;
  }
}

.tour__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 50px;
}
@media (min-width: 768px) {
  .tour__content {
    margin-bottom: 105px;
  }
}
@media (min-width: 1200px) {
  .tour__content {
    margin-bottom: 107px;
  }
}

.tour__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  gap: 10px;
}
@media (min-width: 768px) {
  .tour__list {
    gap: 30px;
  }
}

.tour__item {
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 22px;
}
@media (min-width: 768px) {
  .tour__item {
    font-size: 23px;
    font-weight: 700;
    line-height: 28px;
  }
}

.tour__link {
  display: inline-block;
  position: relative;
  text-decoration: none;
  color: var(--_basic-black);
  cursor: pointer;
}
.tour__link::after {
  content: "";
  height: 2px;
  width: 0;
  position: absolute;
  background-color: var(--_orange);
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  opacity: 0;
  transition: width 0.5s, opacity 0.5s;
}
.tour__link--active {
  color: var(--_orange);
}
.tour__link--active::after {
  content: "";
  height: 2px;
  width: 100%;
  position: absolute;
  background-color: var(--_orange);
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  opacity: 1;
}
@media (min-width: 768px) {
  .tour__link--active {
    /* stylelint-disable-next-line */
  }
  .tour__link--active::after {
    bottom: -5px;
  }
}
@media (any-hover: hover) {
  .tour__link:hover {
    color: var(--_orange);
  }
  .tour__link:hover::after {
    width: 100%;
    opacity: 1;
  }
}

.tour__card__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(318px, 1fr));
  gap: 18px;
}
@media (min-width: 768px) {
  .tour__card__list {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1200px) {
  .tour__card__list {
    gap: 30px;
    grid-template-columns: repeat(3, 1fr);
  }
}

.tour__card {
  padding: 25px;
  min-height: 318px;
}
@media (min-width: 768px) {
  .tour__card {
    min-height: 531px;
    padding: 50px;
  }
}

.tour__card-header h3 {
  margin-bottom: 15px;
}
@media (min-width: 768px) {
  .tour__card-header h3 {
    margin-bottom: 16px;
  }
}

.form {
  border-radius: 30px;
  background-color: var(--_basic-white);
}
@media (min-width: 768px) {
  .form {
    border-radius: 50px;
  }
}

.form__content {
  display: grid;
  grid-template-columns: 1fr;
  padding: 20px 20px 40px;
}
@media (min-width: 768px) {
  .form__content {
    padding: 60px;
  }
}
@media (min-width: 1200px) {
  .form__content {
    padding: 70px;
  }
}

.form__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  gap: 30px;
  margin-bottom: 27px;
}
@media (min-width: 768px) {
  .form__list {
    grid-template-columns: repeat(2, 1fr);
    column-gap: 20px;
    row-gap: 33px;
    margin-bottom: 34px;
  }
}
@media (min-width: 1200px) {
  .form__list {
    column-gap: 30px;
    margin-bottom: 33px;
  }
}

.form__item {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.form__item label {
  font-family: inherit;
  margin-bottom: 8px;
  font-size: 16px;
  font-weight: 400;
  line-height: 23px;
  color: var(--_basic-black);
}
.form__item input {
  width: 100%;
  font-family: inherit;
  font-size: 16px;
  font-weight: 400;
  line-height: 21px;
}
.form__item input::placeholder {
  color: var(--_gray);
}
.form__item input[type=date] {
  text-transform: uppercase;
  appearance: none;
  color: var(--_basic-black);
}
.form__item input[type=date]::-webkit-calendar-picker-indicator {
  opacity: 0;
}
.form__item input[type=date]:required:invalid {
  color: var(--_gray);
}
.form__item select {
  width: 100%;
  appearance: none;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  min-height: 46px;
}
.form__item select option {
  color: var(--_basic-black);
}
.form__item select:required:invalid {
  color: var(--_gray);
}
.form__item textarea {
  resize: vertical;
  min-height: 132px;
  max-width: 100%;
}
@media (min-width: 768px) {
  .form__item label {
    font-size: 20px;
    line-height: 26px;
  }
  .form__item input {
    font-size: 20px;
    line-height: 28px;
  }
  .form__item select {
    font-size: 20px;
    line-height: 28px;
  }
  .form__item textarea {
    min-height: 152px;
  }
}
@media (min-width: 1200px) {
  .form__item textarea {
    min-width: 1030px;
  }
}

.form__field {
  padding: 10px;
  border: 1px solid var(--_beige);
  border-radius: 10px;
  background-color: var(--_semi-beige);
  cursor: pointer;
  outline: 0;
}
@media (any-hover: hover) {
  .form__field:hover {
    background-color: var(--_beige);
  }
}
.form__field:focus-visible {
  border: 1px solid var(--Gray-300, #a6a6a6);
  background-color: var(--_beige);
}
@media (min-width: 768px) {
  .form__field {
    padding: 20px;
  }
}

.form__item-select {
  position: relative;
}
.form__item-select::after {
  content: "";
  position: absolute;
  display: block;
  width: 13px;
  height: 13px;
  right: 17px;
  top: 13px;
  transform: rotate(45deg);
  border: solid var(--_basic-black);
  border-width: 0 2px 2px 0;
}
@media (min-width: 768px) {
  .form__item-select::after {
    right: 27px;
    top: 25px;
  }
}

.form__button-wrapper {
  border: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  margin-bottom: 30px;
}
@media (min-width: 768px) {
  .form__button-wrapper {
    margin-bottom: 31px;
  }
}

.form__legend {
  padding: 0;
  font-family: inherit;
  margin-bottom: 10px;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  color: var(--_basic-black);
}
@media (min-width: 768px) {
  .form__legend {
    font-size: 20px;
    line-height: 25px;
  }
}

.form__checkbox-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  gap: 30px;
}

.form__checkbox-item {
  position: relative;
  display: flex;
  align-items: center;
  padding-left: 30px;
}
.form__checkbox-item span {
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  color: var(--_basic-black);
}
@media (min-width: 768px) {
  .form__checkbox-item {
    padding-left: 37px;
  }
  .form__checkbox-item span {
    font-size: 20px;
    line-height: 28px;
  }
}
@media (min-width: 1200px) {
  .form__checkbox-item {
    padding-left: 37px;
  }
}

.form__checkbox[type=radio] + span {
  position: absolute;
  width: 20px;
  height: 20px;
  left: 1px;
  border: 2px solid var(--_basic-black);
  border-radius: 50%;
  cursor: pointer;
}
@media (min-width: 768px) {
  .form__checkbox[type=radio] + span {
    width: 26px;
    height: 26px;
  }
}

.form__checkbox[type=radio]:checked + span::after {
  content: "";
  position: absolute;
  width: 9px;
  height: 9px;
  background-color: var(--_basic-black);
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
}
@media (min-width: 768px) {
  .form__checkbox[type=radio]:checked + span::after {
    width: 12px;
    height: 12px;
  }
}

.form__checkbox[type=checkbox] + span {
  position: absolute;
  left: 1px;
  width: 20px;
  height: 20px;
  border: 2px solid var(--_basic-black);
  border-radius: 5px;
  cursor: pointer;
}
@media (min-width: 768px) {
  .form__checkbox[type=checkbox] + span {
    width: 26px;
    height: 26px;
  }
}

.form__checkbox[type=checkbox]:checked + span::after {
  content: "";
  position: absolute;
  display: block;
  border-left: 2px solid var(--_basic-black);
  border-bottom: 2px solid var(--_basic-black);
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
  width: 9px;
  height: 6px;
}
@media (min-width: 768px) {
  .form__checkbox[type=checkbox]:checked + span::after {
    width: 12px;
    height: 7px;
  }
}

.form__checkbox-wrapper {
  border: none;
  margin: 0;
  padding: 0;
  margin-bottom: 30px;
}
.form__checkbox-wrapper label {
  display: flex;
  align-items: start;
  position: relative;
  padding-left: 30px;
}
@media (min-width: 768px) {
  .form__checkbox-wrapper {
    margin-bottom: 31px;
  }
  .form__checkbox-wrapper label {
    padding-left: 37px;
  }
}
@media (min-width: 1200px) {
  .form__checkbox-wrapper {
    margin-bottom: 35px;
  }
  .form__checkbox-wrapper label {
    padding-left: 45px;
  }
}

.form__checkbox-text {
  font-size: 16px;
  font-weight: 400;
  line-height: 23px;
  color: var(--_gray);
}
.form__checkbox-text a {
  text-decoration: none;
  font-family: inherit;
  color: var(--_basic-black);
}
@media (any-hover: hover) {
  .form__checkbox-text a:hover {
    text-decoration-line: underline;
  }
}
.form__checkbox-text a:active {
  color: var(--_green);
}
@media (min-width: 768px) {
  .form__checkbox-text {
    font-size: 17px;
    line-height: 28px;
    letter-spacing: -0.17px;
  }
}
@media (min-width: 1200px) {
  .form__checkbox-text {
    font-size: 17px;
    line-height: 24px;
    letter-spacing: -0.17px;
  }
}

.form__button-inner {
  display: flex;
  column-gap: 10px;
  width: 100%;
}
@media (min-width: 768px) {
  .form__button-inner {
    column-gap: 30px;
    max-width: 500px;
  }
}
@media (min-width: 1200px) {
  .form__button-inner {
    column-gap: 30px;
  }
}

.form__submit {
  padding: 13px;
  color: var(--_basic-white);
  background-color: var(--_green);
  transition: background-color, 0.4s ease;
}
@media (any-hover: hover) {
  .form__submit:hover {
    background-color: var(--_dark-green);
  }
}
.form__submit:active {
  background-color: var(--_special-green);
}
@media (min-width: 768px) {
  .form__submit {
    padding: 15px;
  }
}

.form__reset {
  padding-top: 13px;
  color: var(--_green);
  background-color: var(--_semi-green);
  transition: background-color, 0.4s ease;
}
@media (any-hover: hover) {
  .form__reset:hover {
    background-color: var(--_semi-gray);
  }
}
.form__reset:active {
  background-color: var(--_dark-gray);
}

@media (min-width: 768px) {
  .form__item-textarea {
    grid-column: 1/-1;
  }
}

.reviews {
  padding-top: 75px;
}
@media (min-width: 768px) {
  .reviews {
    padding-top: 135px;
  }
}

.reviews__title {
  margin-bottom: 22px;
}
@media (min-width: 768px) {
  .reviews__title {
    margin-bottom: 30px;
  }
}

.reviews__title-wrapper {
  margin: 0 auto;
  max-width: 750px;
  margin-bottom: 51px;
  width: 100%;
}
@media (min-width: 768px) {
  .reviews__title-wrapper {
    margin-bottom: 100px;
  }
}

.reviews__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  gap: 18px;
}
@media (min-width: 768px) {
  .reviews__list {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1200px) {
  .reviews__list {
    gap: 30px;
  }
}

.reviews__item {
  background-color: var(--_beige);
  border-radius: 20px;
  padding: 20px;
  min-height: 408px;
  display: flex;
}
@media (min-width: 768px) {
  .reviews__item {
    padding: 40px;
    min-height: 841px;
    border-radius: 50px;
  }
